.site-footer {
    background-color: var(--color-black);
    color: var(--color-white);
    padding: 10px 0;
    font-size: 1.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  
  .footer-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 30px;
  }
  
  .footer-row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    gap: 10rem;
    margin-top: 50px;
  }
  
  .footer-sub-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10rem;
    width: 100%;
  }
  
  .footer-column {
    flex-grow: 1;
  }
  
  .footer-column-header {
    flex-basis: 100%;
    text-align: center;
    margin-bottom: 30px;
  }
  
  .footer-column-header h2 {
    margin-bottom: 10px;
  }
  
  .footer-column-header p {
    margin: 0;
    line-height: 1.5;
  }
  
  .footer-column h2 {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  
  .footer-column ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .footer-column ul li {
    margin-bottom: 5px;
  }
  
  .footer-column ul li a:hover {
    color: var(--color-primary-variant);
  }
  
  .social-icons {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
  }
  
  .social-icons li {
    margin-right: 10px;
  }
  
  .social-icons li:last-child {
    margin-right: 0;
  }
  
  .fa-facebook-f {
    color: #3b5998;
  }
  
  .fa-tiktok {
    color: #1307c2;
  }
  
  .fa-instagram {
    color: #e4405f;
  }
  
  @media screen and (max-width: 768px) {
    .footer-row {
      flex-direction: column;
      align-items: center;
      gap: 2rem;
    }
  
    .footer-column {
      flex-basis: 100%;
      margin-bottom: 2rem;
    }
  
    .footer-sub-container {
      gap: 2rem;
    }
  
    .social-icons {
      justify-content: center;
    }
  
    .site-footer,
    .footer-column h2,
    .footer-column p,
    .footer-column ul li,
    .footer-text {
      font-size: 0.7rem;
    }
  }
  
  @keyframes rgb {
    0% { color: rgb(255, 0, 0); }
    33% { color: rgb(0, 255, 204); }
    66% { color: rgb(0, 0, 255); }
    100% { color: rgb(255, 0, 0); }
  }
  
  .site-footer h2, .site-footer p {
    animation: rgb 6s linear infinite;
  }
  
  .social-icons a {
    display: inline-block;
    margin: 0 10px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .social-icons a:hover {
    transform: scale(1.2);
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.6);
  }
  
  @keyframes pulse {
    0% { transform: scale(1); opacity: 0.7; }
    50% { transform: scale(1.1); opacity: 1; }
    100% { transform: scale(1); opacity: 0.7; }
  }
  
  .footer-column-links a[href^="#"] {
    display: inline-block;
    transition: all 0.3s ease;
    color: #FFF;
    text-shadow: 0 0 8px rgba(255, 255, 255, 0.7);
  }
  
  .footer-column-links a[href^="#"]:hover {
    animation: pulse 1s infinite;
    color: #E0E0E0;
    text-shadow: none;
  }
  